import React, { Component } from 'react';
import axios from 'axios';


const Form_Style = {
  marginLeft : '10px',
  paddingBottom : '70px',
}


class Form_Add extends Component {
  constructor() {
    super();
    this.state = {
      title: '',
      description: '',
      year: '',
      month: '',
      day: '',
      loading: false,
      message: '',
    };
  }

  onChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
    console.log(e.target.name);
  }

  onSubmit = (e) => {
    e.preventDefault();

    this.setState({
      laoding : true,
    })

    const { title, description, year, month, day } = this.state;

    axios.post('http://localhost:5000/add', { title, description, year, month, day })
  .then(response => {
    this.setState({
      loading : false,
      message : response.title
    })
  })
  .catch(err => {
    console.log(err)
    this.setState({
      loading : false,
    })
  })
  }

  loadOrShowMsg(){
    if(this.state.loading){
      return <p>로딩중...</p>
    } else{
      return <p>{this.state.message}</p>
    }
  }

  render() {
    const { title, description, year, month, day } = this.state;

    return (
      <form style={Form_Style} onSubmit={this.onSubmit}>
        <div>
          계획 : <input
            type="text"
            name="title"
            value={title}
            onChange={this.onChange.bind(this)}
          required/>
        </div>
        <p></p>
        <div>
          계획 설명(생략가능) : <input
            type="text"
            name="description"
            value={description}
            onChange={this.onChange.bind(this)}
          />
        </div>
        <p></p>
        <div>
          년도(ex. 2019) : <input
            type="number"
            name="year"
            value={year}
            onChange={this.onChange.bind(this)}
          required/>
        </div>
        <p></p>
        <div>
          월(ex. 8) : <input
            type="number"
            name="month"
            value={month}
            onChange={this.onChange.bind(this)}
          required/>
        </div>
        <p></p>
        <div>
          일(ex.6) : <input
            type="number"
            name="day"
            value={day}
            onChange={this.onChange.bind(this)}
          required/>
        </div>
        <p></p>
        <button type="submit">제출하기</button>
        {this.loadOrShowMsg()}
      </form>
    );
  }
}


export default Form_Add
